$colgr: #02E602;

@mixin VertText($left: -290px, $top: 403px, $transform: rotate(-90deg)) {
    position: relative;
    display: inline-block;
    width: 458px;
    height: 103px;
    left: $left;
    top: $top;
    font-weight: 700;
    font-size: 100px;
    line-height: 100px;
    text-transform: uppercase;
    color: rgba(70, 70, 70, 0.26);
    text-shadow: 0px 4px 4px #00000040;
    transform: $transform;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'RF Rufo', sans-serif;
    font-weight: 400;
    font-size: 18px;
    color: #fff;
    background-color: #181818;
}

a {
    color: inherit;
    text-decoration: none;
}

li {
    list-style-type: none;
}

.container {
    max-width: 1310px;
    margin: 0 auto;

}

.out {
    position: relative;
    left: 0;
    top: 44px;
    font-weight: 700;
    font-size: 120px;
    color: rgba(255, 255, 255, 0.2);

    span {
        position: absolute;
        left: 6px;
        top: 57px;
        font-weight: 400;
        font-size: 48px;
        color: $colgr;
    }
}

button {
    /*appearance: none;*/
    width: 288px;
    height: 62px;
    margin-left: 45px;
    background-color: #ffffff00;
    border: 1px solid $colgr;
    color: $colgr;
    font-family: 'RF Rufo';
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 28px;
    cursor: pointer;
}
form button{
    width: 197px;
    height: 58px;
}

button:hover {
    background-color: $colgr;
    color: #181818;
}

h4 {
    font-size: 22px;
    font-weight: 400;
    color: #fff;
    margin-top: 5px;
}

.insert {
    font-size: 18px;
    color: #fff;
    font-weight: 300;
    line-height: 31px;
    clear: both;
    margin-top: 21px;

}

ul.breadcrumb {
    padding: 5px 16px;
    list-style: none;

}

ul.breadcrumb li {
    display: inline;
    font-size: 24px;
    color: $colgr;
}

ul.breadcrumb li+li:before {
    padding: 8px;
    color: #fff;
    content: "/\00a0";
}

ul.breadcrumb li a {
    color: #fff;
    text-decoration: none;
}

ul.breadcrumb li a:hover {
    color: $colgr;

}

